import { Component, OnInit } from '@angular/core';
import { IUser } from '../interfaces/user.interface';
import { User } from '../models/user.model';

@Component({
  selector: 'app-list-user',
  templateUrl: './list-user.component.html',
  styleUrls: ['./list-user.component.css']
})
export class ListUserComponent implements OnInit {
  valueLogin: string;
  valuePassword: string;
  valueEmail: string;
  users: IUser[] = [];
  isEdit: boolean;
  index: number;

  constructor() { }

  ngOnInit(): void {
  }

  addUser(): void {

    let USER = new User(this.valueLogin, this.valueEmail, this.valuePassword);
    if (this.valueLogin && this.valueEmail && this.valuePassword) {
      this.users.push(USER);
      console.log(this.users);
      this.reset();
    }
    else {
      alert('Write all field');
    }
  }

  reset(): void {
    this.valueLogin = '';
    this.valuePassword = '';
    this.valueEmail = '';
  }

  edit(index: number): void {
    this.valueLogin = this.users[index].login;
    this.valuePassword = this.users[index].password;
    this.valueEmail = this.users[index].email;
    this.isEdit = true;
    this.index = index;
  }
  delete(index: number): void {
    this.users.splice(index, 1);
  }

  saveUser(): void {
    this.users[this.index].login = this.valueLogin;
    this.users[this.index].password = this.valuePassword;
    this.users[this.index].email = this.valueEmail;
    this.reset();
    this.isEdit = false;
  }
}